class IndexDb {
  constructor() {
    this.version = {
      transaction: 1,
      version: 1,
      address: 1
    }
    this.init();
  }

  async init() {
    let isFirst = await this.isExist({
      indexDbName: 'version',
      key: 'version',
      includeFirst: true
    });
    if(isFirst === false) {
      this.insertData({
        indexDbName: 'version',
        key: 'version',
        data: [{
          transaction: 1,
          version: 1,
          address: 1
        }]
      });
    } else {
      let res = await this.findData({
        indexDbName: 'version',
        key: 'version'
      });
      this.version = res[res.length - 1];
    }
  }

  createDatabase(indexDbName) {
    return new Promise((resolve,reject) => {
      let openRequest = indexedDB.open(indexDbName);
      openRequest.onerror = function (e) { //
        console.log("Database error: " + e.target.errorCode);
        reject(e);
      };
      openRequest.onsuccess = function (event) {
        // console.log("Database open created");
        resolve();
      };
    });
  }

  deleteDatabase(indexDbName) {
    return new Promise((resolve, reject) => {
      let deleteDbRequest = indexedDB.deleteDatabase(indexDbName);
      deleteDbRequest.onsuccess = function (event) {
        // console.log("detete database success");
        resolve();
      };
      deleteDbRequest.onerror = function (e) {
        console.log("Database error: " + e.target.errorCode);
        reject(e);
      };
    })
  }

  addStorageObj({
    indexDbName,
    key,
    keyPath = false
  }) {
    const vm = this;
    return new Promise(async (resolve, reject) => {
      let db;
      let openRequest;
      vm.version[indexDbName] = vm.version[indexDbName] + 1;
      let version = vm.version[indexDbName];
      await vm.insertData({indexDbName: 'version', key: 'version', data: [vm.version]});
      openRequest = indexedDB.open(indexDbName, version);
      openRequest.onerror = function (e) {
        reject();
      }
      openRequest.onsuccess = function (event) {
        console.log("Database created");
      }
      openRequest.onupgradeneeded = function (event) {
        db = event.target.result;
        if (!db.objectStoreNames.contains(key)) {
          let option = {
            autoIncrement: true
          }
          console.log('who quickly?');
          let objectStore = db.createObjectStore(key, option);
          resolve();
        }
      }
    }).catch(e => {
      console.log(e);
    })
  }


  insertData({
    indexDbName,
    key,
    data,
    keyPath = false
  }) {
    const vm = this;
    return new Promise(async (resolve, reject) => {
      // is not exist else to create storageObj
      let isExist = await vm.isExist({
        indexDbName,
        key,
        keyPath
      });
      if(!isExist) {
        await vm.addStorageObj({
          indexDbName,
          key,
          keyPath
        })
      }

      let db;
      let openRequest;
      let version = vm.version[indexDbName];
      openRequest = indexedDB.open(indexDbName, version);
      openRequest.onerror = function (e) {
        console.log("Database error: " + e.target.errorCode);
        reject(e);
      };
      openRequest.onsuccess = function (event) {
        // console.log("Database open created");
        db = openRequest.result;
        let transaction = db.transaction(key, 'readwrite');
        let store = transaction.objectStore(key);
        for (let i = 0; i < data.length; i++) {
          store.add(data[i]); //userinfoobjectstore
        }
        resolve();
        return;
      };
      openRequest.onupgradeneeded = function (event) {
        db = event.target.result;
        //keyobjectstore
        if (!db.objectStoreNames.contains(key)) {
          //keyPath:Javascript
          let option = keyPath === false ? {
            autoIncrement: true
          } : {
            keyPath: keyPath
          }
          db.createObjectStore(key, option);
        }
      }
    }).catch(e => {
      console.log(e);
    })
  }

  findData({
    indexDbName,
    key,
    keyPath = false
  }) {
    const vm = this;
    return new Promise(async function (resolve, reject) {
      // is not exist else to create storageObj
      let isExist = await vm.isExist({
        indexDbName,
        key,
        keyPath
      });
      if(!isExist) {
        await vm.addStorageObj({
          indexDbName,
          key,
          keyPath
        })
      }

      let openRequest; 
      let version = vm.version[indexDbName];
      openRequest = indexedDB.open(indexDbName, version);
      let db;
      openRequest.onerror = function (e) { //
        console.log("Database error: " + e.target.errorCode);
        reject(e)
      };
      openRequest.onsuccess = function (event) {
        // console.log("Database open created");
        db = openRequest.result; //dbdb
        let transaction = db.transaction(key, 'readonly');
        let objectStore = transaction.objectStore(key);
        if (keyPath === false) {
          let cursor = objectStore.openCursor();
          let arr = [];
          cursor.onsuccess = function (e) {
            let res = e.target.result;
            if (!res || res === null) {
              resolve(arr);
              return;
            }
            let request = objectStore.get(Number(res.key)); //id
            request.onsuccess = function (e) {
              let res = e.target.result; //
              if (res) {
                arr.push(res); 
              } else {
                console.log(arr);
                resolve(arr);
                return;
              }
            }
            res.continue();
          }
          cursor.onerror = function (e) {
            console.log(e);
            reject();
          }
        } else {
          let request = objectStore.get(Number(keyPath)); //i=1Number1
          request.onsuccess = function (e) {
            let res = e.target.result; //
            console.log(res);
            resolve(res);
          }
          request.onerror = function(e) {
            reject(e);
          }
        };
      }
    }).catch(e => {
      console.log(e);
    })
  }

  isExist({
    indexDbName,
    key,
    keyPath = false,
    includeFirst = false
  }) {
    const vm = this;
    return new Promise((resolve, reject) => {
      let openRequest;
      let version = vm.version[indexDbName];
      let db;
      openRequest = indexedDB.open(indexDbName, version);
      openRequest.onerror = function (e) { //
        console.log("Database error: " + e.target.errorCode);
        reject(e)
      };
      openRequest.onsuccess = function (event) {
        // console.log("Database open created");
        db = openRequest.result; //dbdb
        // if storageObj not exist; to create
        for(let i = 0;i < db.objectStoreNames.length; i++) {
          if(db.objectStoreNames[i] === key) {
            resolve(true);
          }
        }
        resolve(false);
      }
      openRequest.onupgradeneeded = function (event) {
        db = event.target.result;
        //keyobjectstore
        if (!db.objectStoreNames.contains(key)) {
          //keyPath:Javascript
          let option = keyPath === false ? {
            autoIncrement: true
          } : {
            keyPath: keyPath
          }
          db.createObjectStore(key, option);
          if(includeFirst) resolve(false);
        }
      }
    }).catch(e => {
      console.log(e);
    })
  }

  deleteData({indexDbName, key, keyPath = false}) {
    const vm = this;
    return new Promise(async (resolve, reject) => {
      let version = vm.version[indexDbName]
      let openRequest = indexedDB.open(indexDbName, version);
      let db;
      openRequest.onerror = function (e) {
        console.log(e);
        reject();
      }
      openRequest.onsuccess = function (event) {
        db = openRequest.result; //dbdb
        let transaction = db.transaction(key, 'readwrite');
        let objectStore = transaction.objectStore(key);
        if(keyPath === false) {
          objectStore.clear();
          resolve();
        } else {
          let request = objectStore.delete(Number(keyPath)); //id
          request.onsuccess = function (e) {
            console.log("delete success");
            resolve(e);
          }
          request.onerror = function (e) {
            reject(e);
          }
        }
      }
    }).catch(e => {
      console.log(e);
    })
  }

  updateData({
    indexDbName,
    key,
    data,
    keyPath = false
  }) {
    const vm = this;
    return new Promise(async function(resolve,reject) {
      // is not exist else to create storageObj
      let isExist = await vm.isExist({
        indexDbName,
        key,
        keyPath
      });
      if(isExist) {
        await vm.deleteData({
          indexDbName,
          key,
          data,
          keyPath
        });
      } else {
        await vm.addStorageObj({
          indexDbName,
          key,
          keyPath
        })
      }
      await vm.insertData({
        indexDbName,
        key,
        data,
        keyPath
      });
      resolve();
    }).catch((e) => {
      console.log(e);
    })
  }
}

export default IndexDb;


