const formatTime = date => {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
}
const formatTimeByTimestamp = timestamp => {
  if (!timestamp) return '';
  const date = new Date(timestamp);
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
}

const formatYearMonthDayByTimestamp = timestamp => {
  if (!timestamp) return '';
  const date = new Date(timestamp);
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()

  return [year, month, day].map(formatNumber).join('-');
}

const formatMonthDayByTimestamp = timestamp => {
  if (!timestamp) return '';
  const date = new Date(timestamp);
  const month = date.getMonth() + 1
  const day = date.getDate()

  return [month, day].map(formatNumber).join('-');
}

const formatYearMonthByTimestamp = timestamp => {
  if (!timestamp) return '';
  const date = new Date(timestamp);
  const year = date.getFullYear()
  const month = date.getMonth() + 1

  return [year, month].map(formatNumber).join('-');
}

const formatYearMonthDay = date => {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  return [year, month, day].map(formatNumber).join('-')
}
const formatYearMonthDayHour = date => {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  return [year, month, day].map(formatNumber).join('-') + ', ' + hour + ':00';
}
const formatMonthDay = date => {
  const month = date.getMonth() + 1
  const day = date.getDate()
  return [month, day].map(formatNumber).join('-')
}
const formatHourMinuteSecond = date => {
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()
  return [hour, minute, second].map(formatNumber).join(':')
}
const formatHourMinute = date => {
  const hour = date.getHours()
  const minute = date.getMinutes()
  return [hour, minute].map(formatNumber).join(':')
}
const calculateTimeLeft = (timeStampLeft) => {
  const flag = timeStampLeft >= 0;
  timeStampLeft = Math.abs(timeStampLeft);
  const days = Math.floor(timeStampLeft / (24 * 3600 * 1000)); // 
  let millisecondLeft = timeStampLeft % (24 * 3600 * 1000); // 
  const hours = Math.floor(millisecondLeft / (3600 * 1000)); // 
  millisecondLeft = millisecondLeft % (3600 * 1000); //  
  const minutes = Math.floor(millisecondLeft / (60 * 1000)); // 
  millisecondLeft = millisecondLeft % (60 * 1000); //  
  const seconds = Math.floor(millisecondLeft / 1000); // 
  return {
    days: flag ? days : days - (days * 2),
    hours,
    minutes,
    seconds
  }
}
const calculateTimeFuture = (targettime) => {
  const currentTime = new Date().getTime();
  if (targettime - currentTime > 0 && targettime - currentTime < 86400000 && (new Date(targettime).getDay() - new Date(currentTime).getDay() == 1)) {
    return '\n' + formatHourMinute(new Date(targettime))
  } else if (targettime - currentTime > 0 && targettime - currentTime < 86400000) { // 
    return formatHourMinute(new Date(targettime))
  } else if (currentTime - targettime > 0 && currentTime - targettime < 86400000 && (new Date(currentTime).getDay() - new Date(targettime).getDay() == 1)) {
    return '\n' + formatHourMinute(new Date(targettime))
  } else if (currentTime - targettime > 0 && currentTime - targettime < 86400000) {
    return formatHourMinute(new Date(targettime))
  } else {
    return formatMonthDay(new Date(targettime)) + '\n' + formatHourMinute(new Date(targettime))
  }
}
const formatNumber = n => {
  n = n.toString()
  return n[1] ? n : '0' + n
}
const lastSecondByDate = date => {
  // return Date
  return new Date(new Date(date.toLocaleDateString()).getTime() + 24 * 60 * 60 * 1000 - 1)
}
module.exports = {
  formatTimeByTimestamp,
  formatTime: formatTime,
  formatYearMonthDay,
  formatMonthDay,
  formatHourMinuteSecond,
  formatHourMinute,
  calculateTimeLeft,
  calculateTimeFuture,
  formatYearMonthDayHour,
  lastSecondByDate,
  formatYearMonthDayByTimestamp,
  formatMonthDayByTimestamp,
  formatYearMonthByTimestamp
}


